<template>
    <div id="hexinshuju">
        <div class="jibentiaojian">
            <div class="title">基本指标</div>
                <el-row :gutter="12" style="width:100%;margin:0 auto;display:flex;justify-content:space-between;">
                    <el-col class="s_card" :span="6">
                        <el-card shadow="always">
                            <cards :datainfo="data1"  @click.native="getDetail(data1)"></cards>
                        </el-card>
                    </el-col>
                    <el-col class="s_card" :span="6">
                        <el-card shadow="always">
                            <cards :datainfo="data2"  @click.native="getDetail(data2)"></cards>
                        </el-card>
                    </el-col>
                    <el-col class="s_card" :span="6">
                        <el-card shadow="always">
                            <cards :datainfo="data3" @click.native="getDetail(data3)"></cards>
                        </el-card>
                    </el-col>
                </el-row>
                <el-row :gutter="12" style="width:100%;margin:0 auto;display:flex;justify-content:space-between;margin-top:25px;">
                    <el-col class="s_card" :span="6">
                        <el-card shadow="always">
                            <cards :datainfo="data4" @click.native="getDetail(data4)"></cards>
                        </el-card>
                    </el-col>
                    <el-col class="s_card" :span="6">
                        <el-card shadow="always">
                            <cards :datainfo="data5" @click.native="getDetail(data5)"></cards>
                        </el-card>
                    </el-col>
                    <el-col class="s_card" :span="6">
                    </el-col>
                </el-row>
            </div>
        <div class="zhongyaozhibiao" style="margin-top:50px;">
            <div class="title">重要指标</div>
            <el-row :gutter="12" style="width:95.5%;display:flex;justify-content:center;margin:0 auto;">
                <el-col :span="22">
                    <el-card shadow="always">
                        <cardb :datainfo="data6"></cardb>
                    </el-card>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
import barCharts from './barCharts.vue';
import cards from './card-1';
import cardb from './Card3'
export default {
    name: 'hxsj',
    data(){
        return{
            windowWidth:0,
            data1:{
                title:'学历教育在校生数',
                num1: '1',      //判断传递什么参数
                num:'N/A',
                type:'人',
                tips:'达到1200以上',
                style: 'background:#7CB349;'
            },
            data2:{
                title:'师生比',
                num1: '2',
                num:'N/A',
                type:'',
                tips:'达到1:20以上',
                style: 'background:#505050;'
            },
            data3:{
                title:'专业教师占专任教师占比',
                num1: '3',
                num:'N/A',
                type:'',
                tips: '不低于50%',
                style: 'background:#E42739;'
            },
            data4:{
                title:'校舍建筑面积',
                num1: '4',
                num:'N/A',
                type:'㎡',
                tips: '不低于24000㎡',
                style: 'background:#EF8629;'
            },
            data5:{
                title:'生均仪器设备值',
                num1: '5',
                num:'N/A',
                type:'元/生',
                tips: '工医3000，其它2500',
                style: 'background:#358FE2;'
            },
            data6:[
                {num:'N/A'},
                {num:'N/A'},
                {num:'N/A'},
                {num:'N/A'},
                {num:'N/A'},
                {num:'N/A'},
                {num:'N/A'},
                {num:'N/A'},
                {num:'N/A'},
                {num:'N/A'},
                {num:'N/A'},
                {num:'N/A'},
                {num:'N/A'},
                {num:'N/A'}
            ]
        }
    },
    components:{
        barCharts,
        cards,
        cardb
    },
    methods:{
        getDetail(data){
            var _this = this
            console.log(data)
            if(data.num1 == '1'){
                var data = {reg:'zyzsxsl',style:'2'}
            }else if(data.num1 == '2'){
                var data = {reg:'gzyssb',style:'2'}
            }else if(data.num1 == '3'){
                var data = {reg:'gzyzrjsbl',style:'2'}
            }else if(data.num1 == '4'){
               this.$router.push({path:'/schoolMap'})
            }else if(data.num1 == '5'){
                var data = {reg:'xsyqsbtb',style:'2'}
                var rotate = '0'
            }
            var _this = this
            $.ajax({
                type: "post",
                url: _this.baseUrl+"/ydjxzg/hx/xsgl.action",
                data: data,
                dataType: "json",
                success: function (res) {
                    console.log(res)
                    _this.$router.push({
                        path:'/detail',
                        name:'detail',
                        params:{
                            title: res.title,
                            dataCount1: res.dataCount,
                            dataCount2: res.zyxstbt,
                            param:res.param1,
                            rotate:rotate
                        }
                    })
                },
                error:function(err){
                    console.log(err)
                }
            });
        }
    },
    mounted(){
        var _this = this;
         $.ajax({
            type: "post",
            url: _this.baseUrl+"/ydjxzg/hx/xsgl.action",
            data: {reg:'jbzb',style:'1'},
            dataType: "json",
            success: function (res) {
                console.log(res)
                _this.data1.num = res.totalCountStudent
                _this.data2.num = res.ssb
                _this.data3.num = Math.round((res.zyjsbfb)*100)+'%'
                _this.data4.num = Math.round(res.xsjzmj)
                _this.data5.num = Math.round(res.rjsbzz)

                _this.data6[0].num = Math.round(res.xxzdmj)
                _this.data6[1].num = Math.round(res.sjydmj)
                _this.data6[2].num = Math.round(res.sjxsmj)
                _this.data6[3].num = Math.round(res.tyydmj)
                _this.data6[4].num = res.zrjsCount
                _this.data6[5].num = Math.round((res.gjjsbfb[0].zj)*100)+'%'
                _this.data6[6].num = Math.round((res.doublejsbl.zj)*100)+'%'
                _this.data6[7].num = res.sjzzts
                _this.data6[8].num = res.qklxsl.zcount
                _this.data6[9].num = res.jsylszws.zcount
                _this.data6[10].num = res.xslszws.zcount
                _this.data6[11].num = res.xnsxgws
                _this.data6[12].num = res.xwsxjd
                _this.data6[13].num = res.sjjsjsl
            },
            error:function(err){
                console.log(err)
            }
        });
    }
}
</script>

<style>
#hexinshuju{
    padding: 60px 10px 0px;
}
#hexinshuju .el-col{
    padding: 0!important;
}
.zhongyaozhibiao{
    /* display:flex;
    flex-direction: column;
    align-items: center; */
}
#hexinshuju .title{
    width: 100%;
    height: 18px;
    line-height: 18px;
    font-size: 18px;
    color: #333;
    border-left: 5px solid #029348;
    padding-left: 10px;
    margin-bottom: 40px;
}
#hexinshuju .s_card .el-card{
    min-width: 210px!important;
    max-width: 370px!important;
}
#hexinshuju .s_card .el-card__body{
    min-width: 210px!important;
    max-width: 370px!important;
    padding: 0!important;
}
#hexinshuju .el-card :hover{
    cursor: pointer;
}
#hexinshuju .s_card .el-col-6{
    min-width: 210px!important;
    max-width: 370px!important;
}
#hexinshuju .is-always-shadow{
    -webkit-box-shadow: 2px 2px 5px 1px rgba(0,0,0,.5);
    box-shadow: 2px 2px 5px 1px rgba(0,0,0,.5);
}
</style>



