          </span>
          <p class="tips">参考标准：{{data5.tips}}</p>
        </div>

        <div class="item" @click="getData(data6)">
          <p class="itemName">{{data6.title}}</p>
          <span class="itemNumCon">
            <p class="itemNum itemColor2">{{datainfo[5].num}}</p> {{data6.type}}
          </span>
          <p class="tips">参考标准：{{data6.tips}}</p>
        </div>

        <div class="item" @click="getData(data7)">
          <p class="itemName">{{data7.title}}</p>
          <span class="itemNumCon">
            <p class="itemNum itemColor3">{{datainfo[6].num}}</p> {{data7.type}}
          </span>
          <p class="tips">参考标准：{{data7.tips}}</p>
        </div>

        <div class="item borderNone" @click="getData(data8)">
          <p class="itemName">{{data8.title}}</p>
          <span class="itemNumCon">
            <p class="itemNum itemColor4">{{datainfo[7].num}}</p> {{data8.type}}
          </span>
          <p class="tips">参考标准：{{data8.tips}}</p>
        </div>
      </div>
      <div class="cardItems">
        <div class="item" @click="getData(data9)">
          <p class="itemName">{{data9.title}}</p>
          <span class="itemNumCon">
            <p class="itemNum itemColor1">{{datainfo[8].num}}</p> {{data9.type}}
          </span>
          <p class="tips">参考标准：{{data9.tips}}</p>
        </div>

        <div class="item" @click="getData(data10)">
          <p class="itemName">{{data10.title}}</p>
          <span class="itemNumCon">
            <p class="itemNum itemColor2">{{datainfo[9].num}}</p> {{data10.type}}
          </span>
          <p class="tips">参考标准：{{data10.tips}}</p>
        </div>

        <div class="item" @click="getData(data11)">
          <p class="itemName">{{data11.title}}</p>
          <span class="itemNumCon">
            <p class="itemNum itemColor3">{{datainfo[10].num}}</p> {{data11.type}}
          </span>
          <p class="tips">参考标准:{{data11.tips}}</p>
        </div>

        <div class="item borderNone" @click="getData(data12)">
          <p class="itemName">{{data12.title}}</p>
          <span class="itemNumCon">
            <p class="itemNum itemColor4">{{datainfo[11].num}}</p> {{data12.type}}
          </span>
          <p class="tips">参考标准：{{data12.tips}}</p>
        </div>
      </div>
      <div class="cardItems" @click="getData(data13)">
        <div class="item" style="border-bottom: 1px solid #e4eaec;">
          <p class="itemName">{{data13.title}}</p>
          <span class="itemNumCon">
            <p class="itemNum itemColor1">{{datainfo[12].num}}</p> {{data13.type}}
          </span>
          <p class="tips">参考标准：{{data13.tips}}</p>
        </div>

        <div class="item" style="border-bottom: 1px solid #e4eaec;" @click="getData(data14)">
          <p class="itemName">{{data14.title}}</p>
          <span class="itemNumCon">
            <p class="itemNum itemColor2">{{datainfo[13].num}}</p> {{data14.type}}
          </span>
          <p class="tips">参考标准：{{data14.tips}}</p>
        </div>

      </div>
    </div>
</template>

<script>
export default {
    props:['datainfo'],
    data () {
        return {
          data1:{
            title:'学校占地面积',
            num:'99900',
            num1:'1',
            type:'㎡',
            tips:'不少于40000㎡',
          },
          data2:{
            title:'生均用地指标',
            num:'40',
            num1:'2',
            type:'㎡',
            tips:'不少于33㎡',
          },
          data3:{
            title:'生均校舍面积',
            num:'30',
            num1:'3',
            type:'㎡',
            tips:'不少于20㎡',
          },
          data4:{
            title:'体育用地面积',
            num:'2300',
            num1:'4',
            type:'㎡',
            tips:'暂无',
          },
          data5:{
            title:'专任教师数量',
            num:'80',
            num1:'5',
            type:'人',
            tips:'不少于60人',
          },
          data6:{
            title:'高级技术职务者比例',
            num:'N/A',
            num1:'6',
            type:'',
            tips:'不低于20%',
          },
          data7:{
            title:'双师型教师比例',
            num:'35%',
            num1:'7',
            type:'',
            tips:'不低于30%',
          },
          data8:{
            title:'生均纸质图书',
            num:'35',
            num1:'8',
            type:'册',
            tips:'不少于30册',
          },
          data9:{
            title:'报刊种类',
            num:'103',
            num1:'9',
            type:'种',
            tips:'不低于80种',
          },
          data10:{
            title:'教师阅览室座位数',
            num:'22%',
            num1:'10',
            type:'',
            tips:'不低于20%',
          },
          data11:{
            title:'学生阅览室座位数',
            num:'15%',
            num1:'11',
            type:'',
            tips:'不低于10%',
          },
          data12:{
            title:'校内实训工位数',
            num:'321',
            num1:'12',
            type:'',
            tips:'暂无',
          },
          data13:{
            title:'校外实习基地数量',
            num:'145',
            num1:'13',
            type:'',
            tips:'暂无',
          },
          data14:{
            title:'计算机生均拥有量',
            num:'20',
            num1:'14',
            type:'台/百人',
            tips:'不少于15台/百人',
          },
        }
    },
    methods: {
      getData(data){
        var _this = this
        console.log(data)
        if(data.num1 == '1'){
          this.$router.push({path:'/schoolMap'})
        }else if(data.num1 == '4'){
          this.$router.push({path:'/schoolMap'})
        }else{
          if(data.num1 == '2'){
            var data = {reg:'sjydtbt',style:'2'}
            var rotate = '0'
          }
          if(data.num1 == '3'){
            var data = {reg:'sjxsmjtbt',style:'2'}
            var rotate = '0'
          }
          if(data.num1 == '5'){
            var data = {reg:'gjgslbt',style:'2'}
          }
           if(data.num1 == '6'){
            var data = {reg:'gzyzrjsb',style:'2'}
          }
           if(data.num1 == '7'){
             var data = {reg:'gzyssxjsbl',style:'2'}
           }
          if(data.num1 == '8'){
            var data = {reg:'xxzztstbt',style:'2'}
            var rotate = '0'
          }
           if(data.num1 == '9'){
             var data = {reg:'bkzltbt',style:'2'}
           }
          if(data.num1 == '10'){
             var data = {reg:'jzgylstbt',style:'2'}
           }
           if(data.num1 == '11'){
             var data = {reg:'xsylstbt',style:'2'}
           }
          if(data.num1 == '12'){
            var data = {reg:'xnsxgwstb',style:'2'}
            var rotate = '0'
          }
          if(data.num1 == '13'){
            var data = {reg:'xwjdtb',style:'2'}
            var rotate = '0'
          }
          if(data.num1 == '14'){
            var data = {reg:'jsjtbt',style:'2'}
            var rotate = '0'
          }
          var _this = this
          $.ajax({
              type: "post",
              url: _this.baseUrl+"/ydjxzg/hx/xsgl.action",
              data: data,
              dataType: "json",
              success: function (res) {
                  console.log(res)
                  if(res.type == 'bt'){
                    _this.$router.push({
                      path:'/detail',
                      name:'detail',
                      params:{
                        title: res.title,
                        dataCount1: res.dataCount,
                        param:res.param1,
                        rotate:rotate,
                        type:'bt'
                      }
                    })
                  }
                  _this.$router.push({
                      path:'/detail',
                      name:'detail',
                      params:{
                          title: res.title,
                          dataCount1: res.dataCount,
                          // dataCount2: res.zyxstbt,
                          param:res.param1,
                          rotate:rotate
                      }
                  })
              },
              error:function(err){
                  console.log(err)
              }
          });
        }
        
      }
    },
    mounted() {
     
    },
}
</script>

<style lang="css" scoped>
.card-1 {
    width: 100%;
    min-height: 246px;
    /* margin: 0 auto; */
    padding: 30px 0;
    /* position: relative; */
    color: #a3afb7;
    font-size: 16px;
    display: flex;
    flex-direction: column;
}
.cardItems{
  width: 100%;
  border-top: 1px solid #e4eaec;
}
.item {
    display: inline-block;
    width: 24.7%;
    height: 110px;
    padding-top: 20px;
    border-right: 1px solid #e4eaec;
    float: left;
    position: relative;
}
.itemNumCon {
  display: block;
  width: 100%;
  position: relative;
  top: -20px;
}
.itemName {
    line-height: 30px;
    font-size: 15px;
    font-weight: 900;
    color: #666;
    margin-left: 20%;
    margin-bottom: 30px;
}
.borderNone {
    border-right: none;
}
.itemNum {
    margin-left: 20%;
    font-size: 30px;
    font-style: normal;
    display: inline-block;
}
.tips{
  text-align: center;
  font-size: 11px;
  font-weight: 900;
  text-align: left;
  position: absolute;
  left: 20%;
  bottom: 20px;
}
.itemColor1 {
  color: rgb(53, 143, 226);
}
.itemColor2 {
  color: rgb(124, 179, 73);
}
.itemColor3 {
  color: rgb(228, 39, 57);
}
.itemColor4 {
  color: rgb(239, 134, 41);
}

</style>



